import itemList from "component/item-list.vue"
export default {
	components: {
		itemList
	},
	data() {
		return {
			/* 首页图片 */
			homeImgList: [{
				url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big10001.jpg'
			}, {
				url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big10006.jpg'
			}, {
				url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big99008.jpg'
			},{
				url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big10001.jpg'
			}, {
				url: 'https://ossweb-img.qq.com/images/lol/web201310/skin/big10006.jpg'
			}],
			/* 滚动的Y坐标 */
			isMovingDown: false,
			/* swiper高度 */
			swiperH: '',
			/* 当前标签 */
			tabCur: 0,
			/* 当前轮播 */
			swiperCur: 0,
		}
	},
	onPullDownRefresh() {
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 1000);
	},
	onReady() {
		uni.getSystemInfo({
			success: data => {
				this.swiperH = data.windowHeight
			}
		})
	},
	methods: {
		/* 标签选择 */
		tabSelect(e) {
			this.tabCur = e.currentTarget.dataset.id;
			this.swiperCur = this.tabCur
		},
		/* 轮播变化 */
		swiperChange(e) {
			this.isMovingDown = false
			this.swiperCur = e.detail.current;
			this.tabCur = this.swiperCur
		},
		/* swiper滚动控制 */
		swiperScroll(e) {
			console.log(e)
			if (e.detail.deltaY > 0) {
				this.isMovingDown = false
			} else if (e.detail.deltaY < 0 && e.detail.scrollTop > 80) {
				this.isMovingDown = true
			}
		},
		homeScroll2lower(e) {

		},
		itemScroll2lower(e) {

		},
		homeScroll2upper(e) {

		},
		itemScroll2upper(e) {

		},
		/* 页面跳转 */
		jump2search(){
			uni.navigateTo({
				url:'/pages/search/index'
			})
		},
		/* 辅助功能 */
		previewImage(srcArray, index) {
			let imgArray = srcArray.map(e => e.url)
			uni.previewImage({
				current: index,
				urls: imgArray,
				indicator: true,
				longPressActions: {
					itemList: ['发送给朋友', '保存图片', '收藏'],
					success: function(data) {
						console.log('选中了第' + (data.tapIndex + 1) + '个按钮,第' + (data.index + 1) + '张图片');
					},
					fail: function(err) {
						console.log(err.errMsg);
					}
				}
			});
		},
	}
}
